.clock-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
  
    margin-top: 15px;
  }
  
  .clock {
    position: relative;
    width: 200px;
    height: 200px;
    border-radius: 50%;
    border: 2px solid #000;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    font-family: Arial, sans-serif;
  }
  
  .clock-name {
    width: 200px;
    overflow: hidden;
    text-overflow: ellipsis;
    text-align: center;
    font-weight: 600;
  }
  
  .clock-btn-remove {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
  
    top: 0;
    right: 0;
  
    margin: 0;
    padding: 0;
    width: 22px;
    height: 22px;
  
    font-size: 22px;
    line-height: 22px;
  
  
    background-color: transparent;
    border: 1px solid grey;
    border-radius: 50%;
  
    cursor: pointer;
    transition: transform 0.3s ease;
  }
  
  .clock-btn-remove:hover {
    transform: rotate(380deg);
  }
  
  .clock-btn-remove:active {
    transform: scale(0.8);
  }

  .clock-face {
    position: relative;
  
    width: 180px;
    height: 180px;
  
    background-image: url('data:image/svg+xml;base64,');
    background-size: 90%;
    background-position: center;
    background-repeat: no-repeat;
  
    border: 2px solid #000;
    border-radius: 50%;
  
    user-select: none;
  }
  
  .clock-face:after {
    background: #000;
    border-radius: 50%;
    content: "";
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 5%;
    height: 5%;
    z-index: 10;
  }
  
  .clock-face
  .hour,
  .minute,
  .second {
    border-top-left-radius: 100%;
    border-top-right-radius: 100%;
  
    transition: transform 0.5s ease;
  }
  
  .clock-face
  .hour[style*="rotate(0deg)"],
  .minute[style*="rotate(0deg)"],
  .second[style*="rotate(0deg)"] {
    transition: none;
  }
  
  .second {
    position: absolute;
    height: 45%;
    width: 1%;
    top: 14%;
    left: 49.5%;
    transform-origin: 50% 80%;
  
    background-color: red;
  
    z-index: 8;
  }
  
  .minute {
    position: absolute;
    height: 36%;
    width: 2%;
    top: 14%;
    left: 49%;
    transform-origin: 50% 100%;
  
    background: #000;
    z-index: 7;
  }
  
  .hour {
    position: absolute;
    height: 20%;
    width: 2.5%;
    left: 48.75%;
    top: 30%;
    transform-origin: 50% 100%;
  
    background: #000;
  }